@charset "UTF-8";
*::-webkit-input-placeholder {
  color: #202020;
  opacity: 1;
}

*::-moz-placeholder {
  color: #838383;
  opacity: 1;
}

*::-ms-input-placeholder {
  color: #838383;
  opacity: 1;
}

::placeholder {
  color: #838383;
}

::selection {
  background-color: orange;
  color: #fff;
}

/* font-family: "OpenSansRegular"; */
@font-face {
  font-family: "Poppins-Regular";
  src: url("../fonts/Poppins-Regular/Poppins-Regular.eot?#iefix") format("embedded-opentype"), url("../fonts/Poppins-Regular/Poppins-Regular.woff") format("woff"), url("../fonts/Poppins-Regular/Poppins-Regular.ttf") format("truetype");
  font-style: normal;
  font-weight: normal;
  font-display: fallback;
}

html {
  width: 100%;
  /* height: 100%; */
  font-size: 16px;
  line-height: 1.42;
  margin: 0;
  padding: 0;
  -webkit-backface-visibility: hidden;
  scroll-behavior: smooth;
  /* overflow-x: hidden; */
}

body {
  font-size: 16px;
  min-width: 320px;
  opacity: 1;
  position: relative;
  line-height: 1.42;
  font-family: "OpenSansRegular", sans-serif;
  overflow-x: hidden;
  color: #000000;
}

body input,
body textarea {
  border: #666 1px solid;
  outline: none;
}

body input:focus:required:invalid,
body textarea:focus:required:invalid {
  border-color: red;
}

body input:required:valid,
body textarea:required:valid {
  border-color: green;
}

.img-responsive {
  display: block;
  max-width: 100%;
  height: auto;
}

.img-responsive.img-width {
  width: 100%;
}

.header_fon {
  background: #000 url(../img/hero/1.jpg) center center no-repeat;
  background-size: cover;
  min-height: 100vh;
  background-attachment: fixed;
}

.header_allpage {
  padding: 0;
  margin: 0;
  background-color: #000;
}

.header_allpage .topline {
  background-color: #000;
  position: static;
}

.topline {
  position: fixed;
  position: absolute;
  width: 100%;
  top: 0;
  left: 0;
  border-bottom: 1px solid #423940;
  z-index: 1;
  transition: all 0.55s ease;
  border-bottom: 1px solid #eee0;
}

.topline.fixed {
  position: fixed;
  z-index: 1;
  background-color: #fff;
  animation: fixHeader 1s ease;
}

@keyframes fixHeader {
  0% {
    transform: translateY(-100%);
    opacity: 0;
  }
  100% {
    transform: translateY(0);
    opacity: 1;
  }
}

.ham-burger .fa {
  font-size: 20px;
  color: #000;
  background-color: #fff;
  display: inline-block;
  height: 27px;
  width: 30px;
  line-height: 27px;
  text-align: center;
  cursor: pointer;
  border-radius: 3px;
  display: none;
}

.topline.fixed a.logo {
  color: #000;
}

.topline.fixed a.logo:before {
  background-color: #000;
}

.topline.fixed .navbar ul li a {
  color: #000;
}

.topline.fixed .navbar ul li a:before {
  background-color: #febd01;
}

.topline.fixed .navbar ul li.active a:before {
  content: '';
  height: 3px;
  background-color: #6b0b16;
}

.topline_wrap {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

a.logo {
  display: inline-block;
  text-decoration: none;
  font-size: 22px;
  color: #fff;
  font-family: 'Poppins-Bold', sans-serif;
  text-decoration: none;
  position: relative;
}

a.logo:before {
  content: '';
  height: 3px;
  background-color: #fff;
  width: 100%;
  position: absolute;
  left: 0;
  bottom: 0;
}

.pic {
  display: block;
  max-width: 100%;
  height: auto;
}

.navbar {
  padding: 0;
  margin: 0;
}

.navbar ul {
  padding: 0;
  margin: 0;
  font-size: 0px;
}

.navbar ul li {
  display: inline-block;
}

.navbar ul li a {
  display: inline-block;
  font-size: 15px;
  color: #fff;
  padding: 15px;
  text-decoration: none;
  position: relative;
  transition: all 0.2s ease;
}

.navbar ul li a:before {
  content: '';
  height: 3px;
  background-color: #fff;
  width: 0;
  position: absolute;
  left: 0;
  bottom: 0;
  transition: all 0.2s ease;
}

.navbar ul li a:hover:before {
  width: 100%;
}

.navbar ul li.active a:before {
  content: '';
  height: 3px;
  background-color: #6b0b16;
  width: 100%;
  position: absolute;
  left: 0;
  bottom: 0;
}

.home-content {
  display: flex;
  justify-content: center;
  align-content: center;
  flex-direction: column;
  min-height: 100vh;
}

.header_content {
  align-items: center;
  border-left: 5px solid #fff;
  padding-left: 35px;
}

.header_content h6 {
  color: #fff;
  font-size: 18px;
  font-weight: 500;
  padding: 0;
  margin: 0 0 20px 0;
  line-height: 1.2;
}

.header_content h1 {
  color: #fff;
  font-size: 70px;
  font-weight: 600;
  padding: 0;
  margin: 0 0 25px 0;
  line-height: 1.2;
}

.header_content h3 {
  color: #fff;
  font-size: 25px;
  font-weight: 500;
  padding: 0;
  margin: 0;
  line-height: 1.2;
  margin-bottom: 15px;
}

a.cv_btn {
  margin-top: 20px;
  display: inline-block;
  color: #fff;
  border: 1px solid #fff;
  border-radius: 4px;
  padding: 12px 25px;
  text-decoration: none;
  font-size: 14px;
  text-transform: uppercase;
  font-weight: 600;
  transition: all 0.35s ease;
}

a.cv_btn:hover {
  color: #000;
  background-color: #fff;
}

.block,
.single-block {
  padding: 100px 0;
  background-color: #fff;
}

.block h2 {
  display: inline-block;
  font-size: 35px;
  text-transform: uppercase;
  font-weight: 700;
  color: #000;
  margin: 0 0 10px 0;
  position: relative;
}

.block h2:before {
  content: '';
  position: absolute;
  left: 0;
  right: 30%;
  height: 2px;
  bottom: 0;
  background-color: #febd01;
}

.our_service_title {
  text-align: center;
  margin-bottom: 30px;
}

.our_service_title h2:before {
  left: 30%;
}

.about_me p.small {
  font-size: 14px;
  color: #838383;
  font-weight: 400;
  margin-bottom: 25px;
}

.ttu {
  text-transform: uppercase;
}

.about_me_wrap {
  display: flex;
  justify-content: space-between;
}

.about_me_wrap .pic {
  flex: 0 0 33.33%;
  max-width: 33.33%;
  min-height: 450px;
  background-image: url(../img/about_me.jpg);
  background-repeat: no-repeat;
  background-size: cover;
  background-position: 20% 0;
  transform: rotateY(180deg);
}

.about_me_wrap .about_content {
  flex: 0 0 66.67%;
  max-width: 66.67%;
  padding-left: 33px;
  display: flex;
  align-items: center;
}

.contact_info span {
  font-size: 15px;
  color: #000;
  font-weight: 600;
}

.contact_info a {
  font-size: 15px;
  color: #838383;
  font-weight: 400;
  letter-spacing: 1px;
}

.about_me_wrap .pic img {
  max-width: 100%;
  height: auto;
  display: block;
