}

.about_content h3 {
  font-size: 25px;
  font-weight: 600;
  margin: 0 0 10px 0;
}

.about_content .ac_descr {
  font-size: 18px;
  font-weight: 500;
  color: #000;
  margin: 0 0 35px 0;
}

.contact_info {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
}

.contact_info li {
  width: calc(50% - 15px);
  margin-bottom: 10px;
  list-style-type: none;
}

.our_service {
  background-color: #f5f8fd;
}

.our_service_wrap {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
}

.our_service_item {
