  background-color: #febd01;
  margin: 0 auto 20px;
  border-radius: 50%;
}

.our_service_item .our_service_icon .fa {
  font-size: 25px;
  line-height: 60px;
}

.our_service_item p {
  font-size: 15px;
  font-weight: 400;
  line-height: 24px;
  color: #838383;
  margin: 10px 0 0;
}

.skills_wrap {
  display: flex;
  justify-content: space-between;
}

.skills_wrap_items {
  width: 50%;
  display: flex;
  align-items: center;
}

.skills_wrap_items .pic img {
  max-width: 100%;
  height: auto;
  display: block;
  -webkit-transform: rotateY(180deg);
  transform: rotateY(180deg);
}

.skills_content {
  margin-top: 30px;
  margin-right: 20px;
}

.skill_box {
  max-width: 100%;
  margin-bottom: 25px;
}

.skill_box h6 {
  font-size: 16px;
  font-weight: 500;
  color: #000;
  margin: 0;
}

.skill_bar {
  height: 8px;
  background-color: #eee;
  position: relative;
}

.skill_bar_in {
  background-color: #febd01;
  position: absolute;
  left: 0;
  top: 0;
  height: 8px;
}

.skill_bar_in span {
  right: 0;
