@import "var";

.block,
.single-block {
  padding: 100px 0;
  background-color: #fff;
}

.block h2 {
  display: inline-block;
  font-size: 35px;
  text-transform: uppercase;
  font-weight: 700;
  color: #000;
  margin: 0 0 10px 0;
  position: relative;
}

.block h2:before {
  content: '';
  position: absolute;
  left: 0;
  right: 30%;
  height: 2px;
  bottom: 0;
  background-color: #febd01;
}

.our_service_title {
  text-align: center;
  margin-bottom: 30px;
}

.our_service_title h2:before {
  left: 30%;
}

.about_me p.small {
  font-size: 14px;
  color: #838383;
  font-weight: 400;
  margin-bottom: 25px;
}

.ttu {
  text-transform: uppercase;
}

.about_me_wrap {
  display: flex;
  justify-content: space-between;
}

.about_me_wrap .pic {
  flex: 0 0 33.33%;
  max-width: 33.33%;
  min-height: 450px;
  background-image: url(../img/about_me.jpg);
  background-repeat: no-repeat;
  background-size: cover;
  //  background-size: 50% 40%;
  background-position: 20% 0;
  transform: rotateY(180deg);
}

.about_me_wrap .about_content {
  flex: 0 0 66.67%;
  max-width: 66.67%;
  padding-left: 33px;
  display: flex;
  align-items: center;
}

.contact_info span {
  font-size: 15px;
  color: #000;
  font-weight: 600;
}

.contact_info a {
  font-size: 15px;
  color: #838383;
  font-weight: 400;
  letter-spacing: 1px;
}

.about_me_wrap .pic img {
  max-width: 100%;
  height: auto;
  display: block;
  transform: rotateY(180deg);
}

.about_content h3 {
  font-size: 25px;
  font-weight: 600;
  margin: 0 0 10px 0;
}

.about_content .ac_descr {
  font-size: 18px;
  font-weight: 500;
  color: #000;
  margin: 0 0 35px 0;
}

.contact_info {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
}

.contact_info li {
  width: calc(50% - 15px);
  margin-bottom: 10px;
  list-style-type: none;
}

.our_service {
  background-color: #f5f8fd;
}

.our_service_wrap {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
}

.our_service_item {
  flex: 0 0 calc(33.33% - 20px);
  max-width: calc(33.33% - 20px);
  margin-bottom: 20px;
  border-left: 1px solid #eeeeee;
  background-color: #fff;
  padding: 30px;
  text-align: center;
  position: relative;
}

.our_service_item:before {
  content: '';
  height: 2px;
  position: absolute;
  bottom: 0;
  left: 30%;
  right: 30%;
  background-color: #febd01;
  @include transition(all, 0.3s, ease);
}

.our_service_item:hover:before {
  left: 0;
  right: 0;
}

.our_service_item h5 {
  font-size: 16px;
  font-weight: 600;
  color: #000;
  margin: 0;
}

.our_service_item .our_service_icon {
  height: 60px;
  width: 60px;
  color: #fff;
  background-color: #febd01;
  margin: 0 auto 20px;
  border-radius: 50%;
}

.our_service_item .our_service_icon .fa {
  font-size: 25px;
  line-height: 60px;
}

.our_service_item p {
  font-size: 15px;
  font-weight: 400;
  line-height: 24px;
  color: #838383;
  margin: 10px 0 0;
}

.skills_wrap {
  display: flex;
  justify-content: space-between;
}

.skills_wrap_items {
  width: 50%;
  display: flex;
  align-items: center;
}

.skills_wrap_items .pic img {
  max-width: 100%;
  height: auto;
  display: block;
  -webkit-transform: rotateY(180deg);
  transform: rotateY(180deg);
}

.skills_content {
  margin-top: 30px;
  margin-right: 20px;
}

.skill_box {
  max-width: 100%;
  margin-bottom: 25px;
}

.skill_box h6 {
  font-size: 16px;
  font-weight: 500;
  color: #000;
  margin: 0;
}

.skill_bar {
  height: 8px;
  background-color: #eee;
  position: relative;
}

.skill_bar_in {
  background-color: #febd01;
  position: absolute;
  left: 0;
  top: 0;
  height: 8px;
}

.skill_bar_in span {
  right: 0;
