// portfolio

.portfolio {
  background-color: #f5f8fd;
}

.portfolio .filter_buttons {
  flex: 0 0 100%;
  max-width: 100%;
  margin-bottom: 30px;
}

.portfolio .filter_buttons ul {
  list-style: none;
  text-align: center;
}

.portfolio .filter_buttons ul li {
  display: inline-block;
  margin: 0 5px;
  padding: 5px 10px;
  font-size: 16px;
  font-weight: 500;
  text-transform: uppercase;
  cursor: pointer;
  @include transition(all, 0.3s, ease);
  border-bottom: 2px solid transparent;
}

.portfolio .filter_buttons ul li.active {
  color: #febd01;
  border-bottom: 2px solid #febd01;
  padding: 5px 10px;
}

.portfolio .portfolio_gallery {
  flex: 0 0 100%;
  max-width: 100%;
  display: flex;
  flex-wrap: wrap;
}

.portfolio .portfolio_gallery .item {
  flex: 0 0 33.33%;
  max-width: 33.33%;
  padding: 8px;
  position: relative;
}

.portfolio .portfolio_gallery .item .inner {
  position: relative;
}

.portfolio .portfolio_gallery .item img {
  max-width: 100%;
  height: auto;
  display: block;
}

.portfolio .portfolio_gallery .item .overlay {
  background-color: rgba(0, 0, 0, 0.6);
  position: absolute;
  left: 0;
  top: 0;
  bottom: 0;
  right: 0;
  z-index: 10;
  opacity: 0;
  display: flex;
  flex-wrap: nowrap;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  @include transition(all, 0.3s, ease);
}

.portfolio .portfolio_gallery .item:hover .overlay {
  opacity: 1;
}

.portfolio .portfolio_gallery .item .overlay h4 {
  // background-color: #febd01;
  color: #fff;
  font-size: 19px;
  font-weight: 600;
  margin-top: 5px;
  padding: 5px 7px;
}

.portfolio .portfolio_gallery .item .overlay .fa {
  height: 50px;
  width: 50px;
  line-height: 50px;
  display: inline-block;
  text-align: center;
  color: #fff;
  background-color: #febd01;
  border-radius: 50%;
  cursor: pointer;
}

.portfolio .portfolio_gallery .item:hover .overlay h4 {
  animation: gallerText 0.8s ease;
}

.portfolio .portfolio_gallery .item:hover .overlay .fa {
  animation: gallerIcon 0.8s ease;
}

@keyframes gallerIcon {
  0% {
    transform: translateY(-20px);
    opacity: 0;
  }

  100% {
    transform: translateY(0);
    opacity: 1;
  }
}

@keyframes gallerText {
  0% {
    transform: translateY(20px);
    opacity: 0;
  }

  100% {
    transform: translateY(0);
    opacity: 1;
  }
}

.lightbox {
  position: fixed;
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;
  z-index: 11;
  background-color: rgba(0, 0, 0, 0.6);
  display: flex;
  display: none;
  justify-content: center;
  align-items: center;
  @include transition(display, 0.5s, ease);
}

.lightbox.show {
  display: flex;
}

.lightbox.hide {
  display: none;
}

.lightbox img {
  max-width: 90%;
  max-height: 80%;
  display: block;
}

.lightbox .close-lightbox {
  right: 10px;
  top: 10px;
  position: absolute;
  height: 35px;
  width: 35px;
  line-height: 35px;
  background-color: #fff;
  color: #febd01;
  text-align: center;
  cursor: pointer;
  border-radius: 50%;
  z-index: 101;
}

